import React from 'react'
import { Row, Col } from "react-bootstrap"
import NumericInput from 'react-numeric-input'
class NumberInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.def.defval,
    }
    this.props.def.changeValue = this.setValue.bind(this);
  }

  componentDidMount() {
  }

  onChange = value => {
    if(isNaN(value)) return;
    this.setState({ value })
    this.props.onChange(this.props.def.id,value);
  }

  setValue(value) {
    this.onChange(value);
  }

  render() {
    let ret = (
      <Col className='colnumberinput'>
      <Row noGutters className="dbyellow" >{this.props.def.label} </Row>
      <Row noGutters>
         <NumericInput strict={true} size={this.props.def.size}
            value={this.state.value}
            min={this.props.def.min} max={this.props.def.max} step={this.props.def.step}
            onChange={this.onChange}  
         />
      </Row>
      </Col>
    )
    if(this.props.vertical) {
      ret = (
        <Row>{ret}</Row>
      )
    }
    return ret;
  }
}

export default NumberInput;
